<template>
  <div class="list-box">
    <slot />
    <p v-show="isMore">已经到底了</p>
    <div v-show="isLoadding" class="loading">
      <a-spin tip="Loading..." />
    </div>
  </div>
</template>

<style lang="less" scoped>
@import '../../common/css/your-theme-file.less';

.list-box {
  p {
    text-align: center;
    margin: 10px 0;
    font-size: @text-size;
    color: @msg-color;
  }
  .loading {
    display: flex;
    justify-content: center;
  }
}
</style>

<script>
import { Spin } from 'ant-design-vue'

export default {
  name: 'List',
  components: {
    ASpin: Spin
  },
  props: {
    lowerThreshold: {
      type: Number,
      default: 0
    },
    isLoadding: {
      type: Boolean,
      default: false
    },
    isMore: {
      type: Boolean,
      default: false
    }
  },
  mounted() {
    window.addEventListener('scroll', this.scroll)
  },
  methods: {
    scroll() {
      const { lowerThreshold, isMore } = this
      if (isMore) return
      // 当前滚动条位置（给ie做兼容）
      const wScrollY = window.scrollY || document.documentElement.scrollTop || 0
      // const wScrollyIe = document.documentElement.scrollTop
      // 设备窗口的高度（不会变）
      const wInnerH = window.innerHeight
      // 滚动条总高度
      const bScrollH = document.body.scrollHeight
      if (wScrollY + wInnerH >= bScrollH - lowerThreshold) {
        console.log('到底了.')
        this.$emit('scrolltolower')
      }
    }
  }
}
</script>
